<template>
  <el-cascader
    class="jc-area-cascader"
    v-model="currentValue"
    @input="onInput"
    :options="areas"
    :props="props"
    :placeholder="placeholder"
    :change-on-select="false"
    :disabled="disabled"
    expand-trigger="click"
    @active-item-change="onItemChange"
    :clearable="clearable"
    @blur="onBlur"
  ></el-cascader>
</template>

<script>
import cn from "../../util/cn.json";
import jsonp from "../../util/jsonp.js";
import uuidv1 from "uuid/v1";
export default {
  name: "jc-area",
  props: {
    disabled: Boolean,
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    level: {
      type: Number,
      default: 3,
    },
    action: {
      type: String,
      default: "/eshop/pub/area/china/selectByParentCode",
    },
    props: {
      type: Object,
      default() {
        return {
          value: "areaCode",
          label: "areaName",
          level: "areaLevel",
        };
      },
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    text: Boolean, // 是否返回中文字符 影响性能
    placeholder: String,
  },
  watch: {
    value(val) {
      if (val) {
        if (this.currentValue !== val) {
          this.currentValue = val;
          this.__initItem();
        }
      }
    },
  },
  data() {
    return {
      areas: [],
      id: uuidv1().replace(/\-/g, "_"),
      currentValue: [],
      init: false,
    };
  },
  methods: {
    generateArea() {
      let areas = [];
      this.currentValue.forEach((code) => {
        let area = this.findArea(this.areas, code);
        if (area) {
          areas.push({
            areaCode: code,
            areaName: area.areaName,
            areaLevel: area.areaLevel,
          });
        }
      });
      this.$emit("change", areas);
    },
    findArea(areas, code) {
      if (!areas) return;
      for (let i = 0; i < areas.length; ++i) {
        let area = areas[i];
        if (area[this.props.value] === code) return area;
        if (area.children) {
          let _a = this.findArea(area.children, code);
          if (_a) return _a;
        }
      }
      return null;
    },
    onItemChange(val, level, cb) {
      let code = null;
      if (val && Array.isArray(val) && val.length) code = val[val.length - 1];
      else code = val;
      let area = this.findArea(this.areas, code);
      // 叶子节点不需要去获取
      if (cb && level >= this.level) {
        this.$nextTick(() => this.generateArea());
        this.init = true;
        return;
      } else if (level && level >= this.level) return;
      // 已经存在的不需要再去获取
      if (area && area.children && area.children.length) return;
      if (!code || isNaN(code)) return;
      jsonp(
        `${this.action}/${code}`,
        {
          prefix: `__jc_area_${
            this.id ? this.id : uuidv1().replace(/\-/g, "_")
          }`,
        },
        (data) => {
          if (!data || !data.length) return;
          if (Number(data[0][this.props.level]) < this.level)
            data.forEach((item) => {
              item.children = [];
            });
          let area = this.findArea(this.areas, code);
          if (area) {
            this.$nextTick(() => {
              this.$set(area, "children", data.slice());
            });
          }
          if (cb && level < this.level) {
            cb(this.currentValue[level], level + 1, this.onItemChange);
          } else if (cb) {
            // 初始化完毕
            setTimeout(() => {
              this.generateArea();
              this.init = true;
            }, 500);
          }
        }
      );
    },
    onInput(val) {
      this.currentValue = val;
      let text = [];
      if (this.text) {
        // 是否获取字符
        this.currentValue.forEach((code) => {
          let area = this.findArea(this.areas, code);
          text.push(area.areaName);
        });
        this.generateArea();
      }
      this.$emit("input", this.currentValue, text);
    },
    onBlur() {
      this.$emit("blur");
    },
    __initItem() {
      // if (this.init) return
      let index = this.areas.findIndex(
        (area) => area.areaCode === this.currentValue[0]
      );
      if (index !== -1) this.areas[index].children = [];
      // 预先加载数据
      this.onItemChange(this.currentValue[0], 1, this.onItemChange);
    },
  },
  created() {
    this.areas = JSON.parse(JSON.stringify(cn));
  },
  beforeMount() {
    // 只显示一级
    if (this.level === 1) {
      this.areas.forEach((area) => {
        area.children = undefined;
      });
    }
    this.currentValue = this.value;
  },
  mounted() {
    this.__initItem();
  },
};
</script>

<style>
.jc-area-cascader {
  width: 100%;
}
</style>
