<template>
  <el-col class="module-template" :xs="24" :lg="setLG" :xl="setXL">
    <el-form-item :label="title" :prop="prop">
      <slot></slot>
    </el-form-item>
  </el-col>
</template>

<script>
export default {
  name: "jc-data-grid-search-template",
  props: {
    title: {
      type: String,
    },
    prop: {
      // 相对于 form 的 prop
      type: String,
    },
  },
  data() {
    return {
      itemLength: 1, // 列表长度
      lg: 8,
      xl: 8,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.itemLength = this.$parent.$children.length;
    });
  },
  computed: {
    setLG() {
      let val = this.lg;
      if (this.itemLength == 2) {
        val = 12;
      } else if (this.itemLength == 1) {
        val = 24;
      } else if (this.itemLength == 3) {
        val = 8;
      }

      return val;
    },
    setXL() {
      let val = this.xl;

      if (this.itemLength == 2) {
        val = 12;
      } else if (this.itemLength == 1) {
        val = 24;
      } else if (this.itemLength == 3) {
        val = 8;
      }

      return val;
    },
  },
};
</script>

<style lang="scss" scoped>
.module-template {
  height: 50px;
}

/deep/ .el-form-item {
  display: flex !important;
  align-items: center;
  width: 100%;
}

/deep/ .el-form-item__label {
  max-width: 150px;
  flex-shrink: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/deep/ .el-form-item__content {
  width: 100%;
}
</style>
