<template>
  <div class="editor-wrapper" :class="{ split: split }">
    <div ref="toolbar"></div>
    <div ref="editor" class="editor"></div>
  </div>
</template>

<script>
import "wangeditor/release/wangEditor.min.css";
const Editor = require("wangeditor");
export default {
  name: "jc-editor",
  props: {
    value: {
      type: String,
      default: "",
    },
    split: {
      type: Boolean,
      default: false,
    },
    /**
     * 绑定的值的类型, enum: ['html', 'text']
     */
    valueType: {
      type: String,
      default: "html",
    },
    /**
     * @description 设置change事件触发时间间隔
     */
    changeInterval: {
      type: Number,
      default: 200,
    },
    /**
     * @description 是否开启本地存储
     */
    cache: {
      type: Boolean,
      default: true,
    },
    disabled: Boolean,
  },
  computed: {
    editorId() {
      return `editor${this._uid}`;
    },
    isHtml() {
      return this.valueType === "html";
    },
  },
  watch: {
    value: {
      handler() {
        if (this.editor) {
          this.setHtml(this.value);
          // this.editor.txt.html(this.value)
        } else {
          this.initEditor();
          // this.editor.txt.html(this.value)
          this.setHtml(this.value);
        }
      },
    },
  },
  data() {
    return {
      editor: null,
    };
  },
  methods: {
    setHtml(val) {
      console.log(this.editor.txt.html());
      console.log(val);
      // var range = window.getSelection().getRangeAt(0);
      if (val !== this.editor.txt.html()) {
        this.editor.txt.html(val);
      }

      // range.insertNode(this.$refs.editor);
    },
    initEditor() {
      if (this.editor) return;
      if (!this.split) {
        this.editor = new Editor(this.$refs.editor);
      } else {
        this.editor = new Editor(this.$refs.toolbar, this.$refs.editor);
      }
      this.editor.change = this.editor.customConfig.onchange = () => {
        let text = this.editor.txt.text();
        let html = this.editor.txt.html();
        if (this.cache) localStorage.editorCache = html;
        this.$emit("input", this.valueType === "html" ? html : text);
        this.$emit("on-change", html, text);
      };
      this.editor.customConfig.onchangeTimeout = this.changeInterval;
      this.editor.customConfig.pasteFilterStyle = false;
      this.editor.customConfig.uploadImgServer =
        "/eshop/pub/file/upload/addRichTextEdit"; // 富文本专属上传接口
      this.editor.customConfig.uploadFileName = "file";
      // create这个方法一定要在所有配置项之后调用
      this.editor.create();
      this.setHtml(this.value);
      if (this.disabled) {
        this.editor.$textElem.attr("contenteditable", false); // 禁用编辑框
      }
    },
  },
  mounted() {
    this.initEditor();
  },
};
</script>

<style lang="scss">
.editor-wrapper {
  .w-e-text-container {
    border: 1px solid #e7e7eb !important;
    border-top: 0 !important;
  }
  .w-e-toolbar {
    background: #fff !important;
    border-color: #e7e7eb !important;
  }
  &.split {
    .w-e-toolbar {
      border: 1px solid #e7e7eb;
      flex-wrap: wrap;
    }
    .editor {
      border: 1px solid #e7e7eb;
    }
  }
  .w-e-droplist {
    z-index: 20000000 !important;
    background: #fff;
  }
  .w-e-toolbar {
    z-index: unset !important;
  }
  .w-e-menu,
  .w-e-text-container {
    z-index: unset !important;
  }
}
</style>
