<template>
  <div class="jc-areas-cascader">
    <el-select
      ref="dataProvince"
      @change="provinceChange"
      v-model="select.provinceCode"
      :style="{ width: style.selectWidth + 'px' }"
      class="select"
      placeholder="请选择省份"
    >
      <el-option
        v-for="item in provinces"
        :key="item[props.value]"
        :label="item[props.label]"
        :value="item[props.value]"
      ></el-option>
    </el-select>
    <el-select
      ref="dataCity"
      @change="cityChange"
      v-model="select.citysCode"
      :style="{ width: style.selectWidth + 'px' }"
      class="select"
      placeholder="请选择市"
    >
      <el-option
        v-for="item in citys"
        :key="item[props.value]"
        :label="item[props.label]"
        :value="item[props.value]"
      ></el-option>
    </el-select>
    <el-select
      ref="dataDistrict"
      @change="districtChange"
      v-model="select.districtsCode"
      :style="{ width: style.selectWidth + 'px' }"
      class="select"
      placeholder="请选择区域"
    >
      <el-option
        v-for="item in districts"
        :key="item[props.value]"
        :label="item[props.label]"
        :value="item[props.value]"
      ></el-option>
    </el-select>
    <div class="address-detail" v-if="address">
      <el-input
        placeholder="请输入详细地址"
        :style="{ width: style.inputWidth + 'px' }"
        :value="addressDetail"
        @input="addressChange"
      ></el-input>
    </div>
  </div>
</template>
<script>
import cn from "../../util/cn.json";
import jsonp from "../../util/jsonp.js";
import uuidv1 from "uuid/v1";
export default {
  name: "jc-area-detail",
  props: {
    disabled: Boolean,
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    level: {
      type: Number,
      default: 3,
    },
    action: {
      type: String,
      default: "/eshop/pub/area/china/selectByParentCode",
    },
    props: {
      type: Object,
      default() {
        return {
          value: "areaCode",
          label: "areaName",
          level: "areaLevel",
        };
      },
    },
    text: Boolean, // 是否返回中文字符 影响性能
    placeholder: String,
    styles: Object,
    addressText: String,
    address: Boolean,
  },
  watch: {
    styles: {
      immediate: true,
      deep: false,
      handler(val) {
        this.style = Object.assign(this.style, val);
      },
    },
    value(val) {
      if (!this.init) {
        this.currentValue = val;
        this.provinceChange(this.currentValue[0]);
      }
    },
    addressText: {
      immediate: true,
      handler(val) {
        this.addressDetail = val;
      },
    },
  },
  data() {
    return {
      areas: [],
      id: uuidv1().replace(/\-/g, "_"),
      currentValue: [],
      currentText: [],
      init: false,
      style: {
        selectWidth: 157,
        inputWidth: 500,
      },
      provinces: [],
      citys: [],
      districts: [],
      addressDetail: "",
      select: {
        provinceCode: "",
        citysCode: "",
        districtsCode: "",
      },
    };
  },
  methods: {
    addressChange(val) {
      this.addressDetail = val;
      this.updateCurrentValue({
        area: this.currentValue,
        address: this.addressDetail,
      });
    },
    provinceChange(val) {
      // 省级改变
      if (this.init) {
        // 初始化完成后改变事件清空当前值
        this.currentValue = [];
        this.citys = [];
        this.districts = [];
      }
      this.currentValue[0] = val;
      this.select.provinceCode = val;
      let area = this.areas.find((item) => item[this.props.value] === val);
      this.currentText[0] = area[this.props.label];
      if (area.children && area.children.length) {
        this.citys = area.children;
        if (this.currentValue && this.currentValue.length > 2) {
          this.select.citysCode = this.currentValue[1];
        } else {
          this.select.citysCode = this.citys[0][this.props.value];
        }
        this.cityChange(this.select.citysCode);
      } else {
        if (this.currentValue && this.currentValue.length > 1 && !this.init) {
          this.getAreaChildren(
            "citys",
            val,
            area,
            this.cityChange,
            this.currentValue[1]
          );
        } else {
          this.getAreaChildren("citys", val, area, this.cityChange);
        }
      }
    },
    cityChange(val) {
      // 市级改变
      this.currentValue[1] = val;
      if (this.init) {
        // 初始化完成后改变事件清空当前值
        this.districts = [];
        this.select.districtsCode = "";
      }
      let oneAreas = this.areas.find(
        (item) => item[this.props.value] === this.select.provinceCode
      );
      let twoAreas = oneAreas.children.find(
        (item) => item[this.props.value] === val
      );
      this.currentText[1] = twoAreas[this.props.label];
      if (twoAreas.children && twoAreas.children.length) {
        this.districts = twoAreas.children;
        if (this.currentValue && this.currentValue.length > 2) {
          this.select.districtsCode = this.currentValue[2];
        } else {
          this.select.districtsCode = this.districts[0][this.props.value];
        }
        this.districtChange(this.select.districtsCode);
      } else {
        if (this.currentValue && this.currentValue.length > 2 && !this.init) {
          this.getAreaChildren(
            "districts",
            val,
            twoAreas,
            this.districtChange,
            this.currentValue[2]
          );
        } else {
          this.getAreaChildren("districts", val, twoAreas, this.districtChange);
        }
      }
    },
    districtChange(val) {
      // 区域改变
      this.currentValue[2] = val;
      this.init = true;
      let districts = this.districts.find(
        (item) => item[this.props.value] === val
      );
      this.currentText[2] = districts[this.props.label];
      if (this.address && this.text) {
        this.updateCurrentValue({
          area: this.currentValue,
          areaText: this.currentText,
          address: this.addressDetail,
        });
      } else if (this.text) {
        this.updateCurrentValue({
          area: this.currentValue,
          areaText: this.currentText,
        });
      } else if (this.address) {
        this.updateCurrentValue({
          area: this.currentValue,
          address: this.addressDetail,
        });
      } else {
        this.updateCurrentValue({ area: this.currentValue });
      }
    },
    getAreaChildren(setString, code, area, cb, curCode = undefined) {
      jsonp(
        `${this.action}/${code}`,
        {
          prefix: `__jc_area_${
            this.id ? this.id : uuidv1().replace(/\-/g, "_")
          }`,
        },
        (data) => {
          data.forEach((item) => {
            item.children = [];
          });
          this[setString] = data;
          this.select[setString + "Code"] =
            curCode || data[0][this.props.value];
          this.$set(area, "children", data);
          if (cb) {
            cb(this.select[setString + "Code"]);
          }
          return data;
        }
      );
    },
    updateCurrentValue(area) {
      // 更新当前值
      this.$emit("input", area.area, area);
    },
  },
  created() {
    this.areas = JSON.parse(JSON.stringify(cn));
    this.provinces = this.areas;
    if (this.value && this.value.length) {
      this.select.provinceCode = this.value[0];
      this.currentValue = this.value;
      this.provinceChange(this.select.provinceCode);
    }
  },
};
</script>

<style lang="scss">
.jc-areas-cascader {
  .address-detail {
    margin-top: 20px;
  }
  .select {
    margin-left: 10px;
    &:first-child {
      margin-left: 0px;
    }
  }
}
</style>
