<template>
  <div class="jc-tab-data-grid">
    <template v-if="!noAuth">
      <div class="tab-wrapper">
        <el-tabs v-model="activeName" @tab-click="handleTabClick">
          <template v-for="tab in tabs">
            <el-tab-pane
              v-if="$has(tab.code)"
              :key="tab.name"
              :name="tab.name"
              :disabled="tab.disabled"
              ><!--
                -->
              <div slot="label">
                <span>{{ tab.label }}</span>
                <jc-badge :num="tab.num"></jc-badge>
              </div>
            </el-tab-pane>
          </template>
        </el-tabs>
      </div>
      <jc-data-grid-head
        v-if="!diy"
        :custom-body="customBody"
        ref="container"
        style="height: calc(100% - 50px)"
      >
        <div class="searbar-wrapper" slot="search">
          <slot name="searchbar" />
        </div>
        <div class="header-wrapper" slot="header">
          <slot name="header" />
        </div>
        <jc-table-template
          :total="total"
          @current-change="handleCurrentChange"
          @selection-change="selectChange"
          @size-change="handleSizeChange"
          :current-page="pageNum"
          :fixed="false"
          :pagination-disabled="loading"
          :page-size="pageSize"
        >
          <el-table
            stripe
            tooltip-effect="dark"
            show-overflow-tooltip
            :show-summary="showSummary"
            :sum-text="sumText"
            :summary-method="summaryMethod"
            :span-method="spanMethod"
            @current-change="onTableCurrentChange"
            @sort-change="onTableSortChange"
            @select-all="onTableSelectAll"
            @selection-change="onTableSelectionChange"
            @row-dbclick="onTableRowDbClick"
            @filter-change="onTableFilterChange"
            highlight-current-row
            ref="table"
            :row-key="rowKey"
            :empty-text="emptyText"
            :key="activeName"
            height="calc(100% - 64px)"
            v-loading="loading"
            :data="list"
          >
            <slot />
            <template slot="append">
              <slot name="append" />
            </template>
          </el-table>
          <template slot="btns">
            <slot name="btns" />
          </template>
        </jc-table-template>
        <template slot="customBody">
          <slot name="custom-body"></slot>
        </template>
        <slot name="modal" />
      </jc-data-grid-head>
      <div v-if="diy" class="diy">
        <slot name="diy" />
      </div>
    </template>
    <error-page v-else :show-btn="false" />
  </div>
</template>

<script>
import ErrorPage from "@/view/error-page/401.vue";
import Mixins from "../../util/dataGridMixins";
export default {
  name: "jc-tab-data-grid",
  mixins: [Mixins],
  props: {
    selectApi: {
      // 优先级比较低 若tabs没有api属性，则会尝试select-api
      type: String,
      required: false,
    },
    tabs: {
      type: Array,
      required: true,
    },
    searchKey: {
      type: Boolean,
      default: true,
    },
    value: [Number, String],
    rowKey: [Function, String],
    customBody: {
      type: Boolean,
      default: false,
    },
    spanMethod: [Function],
  },
  components: {
    ErrorPage,
  },
  data() {
    return {
      diy: false,
      activeName: "",
      api: "",
      noAuth: false,
      init: false, // 初始化
    };
  },
  watch: {
    value: {
      immediate: true,
      handler() {
        if (this.value) {
          this.activeName = this.value;
          this.api = this.getApi();
        }
      },
    },
  },
  created() {
    if (this.init) return;
    // 设置默认的tab
    let t = this.tabs.filter((t) => this.$has(t.code));
    if (t && t.length) {
      if (!this.activeName) {
        this.activeName = t[0].name;
      }
      if (this.activeName !== this.value) this.$emit("input", this.activeName);
    } else {
      this.noAuth = true;
    } // 没有权限
    if (this.autoSearch) {
      if (!this.api) {
        this.api = this.getApi();
      }
      this.select(true);
    }
  },
  activated() {
    if (this.init) {
      if (this.autoSearch) {
        this.select(false);
      }
    } else {
      this.init = true;
    }
  },
  methods: {
    selectChange(row) {
      this.$emit("toSelect", row);
    },
    clear() {
      this.$set(this, "list", []);
      this.pageNum = 1;
      this.pageSize = 25;
      this.total = 0;
    },
    /**
     * @description 从tabs和select-api中获取api
     * @author yangyu
     */
    getApi() {
      for (let i = 0; i < this.tabs.length; ++i) {
        let tab = this.tabs[i];
        if (this.activeName === tab.name && tab.diy) {
          this.diy = true;
          return false;
        } else if (this.activeName === tab.name && tab.api) {
          this.diy = false;
          return tab.api;
        }
      }
      this.diy = false;
      return this.selectApi;
    },
    select(reload, params) {
      return this.__select(reload, params, this.api);
    },
    handleTabClick(tab) {
      this.$emit("input", this.activeName);
      this.api = this.getApi();
      this.triggerResize();
      this.$nextTick(() => {
        // 监听大小改变
        if (this.searchKey) {
          if (this.$slots.searchbar && this.$slots.searchbar.length) {
            this.$slots.searchbar.forEach((searchbar) => {
              if (
                searchbar.componentInstance &&
                searchbar.componentInstance.handleResize
              ) {
                searchbar.componentInstance.handleResize();
              }
            });
          }
        }
        // if (this.$slots.searchbar && this.$slots.searchbar.length) {
        //   this.$slots.searchbar.forEach(searchbar => {
        //     if (searchbar.componentInstance && searchbar.componentInstance.handleResize) {
        //     searchbar.componentInstance.handleResize()
        //     }
        //   })
        // }
      });
      if (this.autoSearch) {
        this.select(true);
      } else {
        this.clear();
      }
      this.$emit("tab-click", tab);
    },
    triggerResize() {
      if (this.$refs.container) this.$refs.container.triggerResize();
    },
  },
};
</script>

<style lang="scss">
.jc-tab-data-grid {
  height: 100%;
}

.tab-wrapper {
  padding: 0 20px;
  background: #fff;
  // margin-bottom: 20px;
  // border: 1px solid #e4e7ed;
  .el-tabs__header {
    margin-bottom: 0;
  }

  .el-tabs__nav-wrap::after {
    height: 0px;
  }
}

.diy {
  height: calc(100% - 49px);
  display: flex;
}
</style>
