<template>
  <jc-data-grid-head :custom-body="customBody" ref="container" v-preview>
    <div class="title-wrapper" slot="title">
      <slot name="title" />
    </div>
    <div class="searbar-wrapper" slot="search">
      <slot name="searchbar" />
    </div>
    <div class="header-wrapper" slot="header">
      <slot name="header" />
    </div>
    <div style="height: 100%">
      <jc-table-template
        :total="total"
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
        :current-page="pageNum"
        :fixed="false"
        :pagination-disabled="loading"
        :page-size="pageSize"
      >
        <slot name="tableHeader" />
        <el-table
          stripe
          tooltip-effect="dark"
          :row-key="rowKey"
          show-overflow-tooltip
          :show-summary="showSummary"
          :sum-text="sumText"
          :empty-text="emptyText"
          :summary-method="summaryMethod"
          :span-method="spanMethod"
          highlight-current-row
          @current-change="onTableCurrentChange"
          @sort-change="onTableSortChange"
          @select-all="onTableSelectAll"
          @selection-change="onTableSelectionChange"
          @row-dbclick="onTableRowDbClick"
          @filter-change="onTableFilterChange"
          ref="table"
          v-loading="loading"
          :data="list"
          height="calc(100% - 54px)"
        >
          <slot />
        </el-table>
        <template slot="btns">
          <slot name="btns" />
        </template>
      </jc-table-template>
    </div>
    <slot name="modal" />
    <template slot="customBody">
      <slot name="custom-body"></slot>
    </template>
  </jc-data-grid-head>
</template>

<script>
export default {
  name: "jc-data-grid",
  props: {
    selectApi: {
      // 数据查询api
      type: String,
      required: false,
    },
    params: {
      // 查询参数
      type: Object,
      default() {
        return {};
      },
    },
    autoSearch: {
      // 是否自动查询
      type: Boolean,
      default: true,
    },
    showSummary: {
      type: Boolean,
      default: false,
    },
    rowKey: [Function, String],
    sumText: {
      type: String,
      default: "合计",
    },
    summaryMethod: {
      type: Function,
    },
    spanMethod: {
      type: Function,
    },
    customBody: {
      type: Boolean,
      default: false,
    },
    emptyText: {
      type: String,
      default: "暂无数据",
    },
  },
  data() {
    return {
      pageNum: 1, // 当前页
      pageSize: 25, // 显示条数
      total: 0,
      list: [],
      extra: {}, //列表需要的额外返回值集合，例如当前页面总和
      init: false, // 初始化
      loading: false,
      query: {},
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this.select();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.select();
    },
    /**
     * @description 查询
     * @author yangyu
     */
    select(reload) {
      if (!this.selectApi) return;
      if (reload) this.pageNum = 1;
      this.loading = true;
      return this.$api(this.selectApi, {
        pageSize: this.pageSize,
        pageNum: this.pageNum,
        ...this.params,
      })()
        .then((res) => {
          if (res) {
            this.list = res.page.list;
            this.query = res.query;
            this.extra = res.extra ? res.extra : {};
            this.$emit("update:payload", this.query);
            this.$emit("update:res", res);
            this.pageNum = res.page.pageNum;
            this.pageSize = res.page.pageSize;
            this.total = res.page.total;
            this.$nextTick(() => {
              if (this.$refs.table) {
                this.$refs.table.bodyWrapper.scrollTop = 0;
              }
            });
          }
          this.loading = false;
        })
        .catch((err) => {
          this.alert(err, "error");
          // this.loading = false // 数据加载失败不停止加载效果
        });
    },
    /**
     * element-ui 表格移植
     * @author yangyu
     */
    /**
     * 当前行改变
     * @param {*} currentRow
     * @param {*} oldCurrentRow
     */
    onTableCurrentChange(currentRow, oldCurrentRow) {
      this.$emit("table-current-change", currentRow, oldCurrentRow);
    },
    /**
     * 表格排序发生改变
     * @param {*} params
     */
    onTableSortChange(params) {
      this.$emit("table-sort-change", params);
    },
    /**
     * 表格全选触发
     * @param {*} selection
     */
    onTableSelectAll(selection) {
      this.$emit("table-select-all", selection);
    },
    /**
     * 表格多选框切换触发
     * @param {*} selection
     */
    onTableSelectionChange(selection) {
      this.$emit("table-selection-change", selection);
    },
    /**
     * 表格列过滤发生改变
     * @param {*} filters
     */
    onTableFilterChange(filters) {
      this.$emit("table-filter-change", filters);
    },
    /**
     * 表格行双击
     * @param {*} row
     * @param {*} event
     */
    onTableRowDbClick(row, event) {
      this.$emit("table-row-dbclick", row, event);
    },
    /**
     * 用于多选表格，清空用户的选择
     */
    clearTableSelection() {
      if (this.$refs.table) this.$refs.table.clearSelection();
    },
    /**
     * 用于多选表格，切换某一行的选中状态，如果使用了第二个参数，
     * 则是设置这一行选中与否（selected 为 true 则选中）
     * @param {*} row
     * @param {Boolean} selected
     */
    toggleTableRowSelection(row, selected) {
      if (this.$refs.table) this.$refs.table.toggleRowSelection(row, selected);
    },
    /**
     * 用于多选表格，切换所有行的选中状态
     */
    toggleTableAllSelection() {
      if (this.$refs.table) this.$refs.table.toggleAllSelection();
    },
    /**
     *  用于可展开表格，切换某一行的展开状态，如果使用了第二个参数，则是设置这一行展开与否（expanded 为 true 则展开）
     * @param {*} row
     * @param {Boolean} expanded
     */
    toggleTableRowExpansion(row, expanded) {
      if (this.$refs.table) this.$refs.table.toggleRowExpansion(row, expanded);
    },
    /**
     * 用于单选表格，设定某一行为选中行，如果调用时不加参数，则会取消目前高亮行的选中状态。
     * @param {*} row
     */
    setTableCurrentRow(row) {
      if (this.$refs.table) this.$refs.table.setCurrentRow(row);
    },
    /**
     * 用于清空排序条件，数据会恢复成未排序的状态
     */
    clearTableSort() {
      if (this.$refs.table) this.$refs.table.clearSort();
    },
    /**
     * 不传入参数时用于清空所有过滤条件，数据会恢复成未过滤的状态，
     * 也可传入由columnKey组成的数组以清除指定列的过滤条件
     * @param {*} columnKey
     */
    clearTableFilter(columnKey) {
      if (this.$refs.table) this.$refs.table.clearFilter(columnKey);
    },
    /**
     * 手动对 Table 进行排序
     * @param {String} prop 指定排序列
     * @param {String} order 指定排序顺序
     */
    tableSort(prop, order) {
      if (this.$refs.table) this.$refs.table.sort(prop, order);
    },
    /**
     * 对 Table 进行重新布局。当 Table 或其祖先元素由隐藏切换为显示时，可能需要调用此方法
     */
    doTableLayout() {
      if (this.$refs.table) this.$refs.table.doLayout();
    },
  },
  created() {
    if (this.init) return;
    if (this.autoSearch) this.select(true);
  },
  activated() {
    if (this.init) {
      if (this.autoSearch) this.select(false);
    } else this.init = true;
  },
};
</script>
