<template>
  <div class="data-grid-search-form">
    <el-row ref="row">
      <el-col ref="search" :span="formSpan">
        <el-form
          :label-width="labelWidth"
          inline
          ref="form"
          :model="model"
          :rules="rules"
        >
          <slot />
        </el-form>
      </el-col>
      <el-col :span="btnSpan">
        <el-form class="data-grid-search-form-btn">
          <el-form-item>
            <div :style="{ marginLeft: `${marginLeft}px` }">
              <slot name="btn" />
            </div>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  addResizeListener,
  removeResizeListener,
} from "../../util/resize-manager";
import debounce from "lodash.debounce";
function formatDecimal(num, decimal) {
  num = num.toString();
  let index = num.indexOf(".");
  if (index !== -1) {
    num = num.substring(0, decimal + index + 1);
  } else {
    num = num.substring(0);
  }
  return Number(parseFloat(num).toFixed(decimal));
}

function getComputedStyle(dom, attr) {
  return document.defaultView.getComputedStyle(dom, null)[attr];
}

const MARGIN_LEFT = 56;

export default {
  name: "jc-data-grid-search-form",
  props: {
    model: {
      type: Object,
      default() {
        return {};
      },
    },
    rules: {
      type: [Object],
      default() {
        return {};
      },
    },
    labelWidth: {
      type: String,
      default: "",
    },
    span: {
      type: Number,
      default: 20,
    },
  },
  data() {
    return {
      marginLeft: 0, // 按钮组距离输入框组的距离 公式：MARGIN_LEFT - blankWidth
      row: null,
      formSpan: 20,
      btnSpan: 4,
      blankWidth: 0,
      colWidth: 0, // 每一列的宽度
    };
  },
  methods: {
    getForm() {
      return this.$refs.form;
    },
    __handle() {
      // if (this.timer) clearTimeout(this.timer)
      // this.timer = setTimeout(() => {
      this.handleResize();
      // }, 20)
    },
    handleResize() {
      // 列宽
      this.colWidth = (this.row.offsetWidth / 24).toFixed(5);
      // 计算空白区域宽度
      let children = this.$refs.form.$children;
      let threshold = formatDecimal(this.span * this.colWidth, 3);
      let total = 0;
      for (let i = 0; i < children.length; ++i) {
        let width =
          Number(getComputedStyle(children[i].$el, "width").replace("px", "")) +
          Number(
            getComputedStyle(children[i].$el, "marginRight").replace("px", "")
          );
        if (total + width > threshold) {
          break;
        }
        total += width;
      }
      // 空白区域宽度
      let blankWidth = threshold - total;
      // 计算可以减去的列宽
      let diff = Math.floor(blankWidth / this.colWidth);
      this.formSpan = this.span - diff;
      this.btnSpan = 24 - this.formSpan;
      // 为了计算margin-left 需要重新计算空白区域宽度
      this.$nextTick(() => {
        this.blankWidth = this.$refs.search.$el.offsetWidth - total;
        this.marginLeft = MARGIN_LEFT - this.blankWidth;
      });
    },
  },
  mounted() {
    this.handleResize = debounce(this.handleResize, 20, { leading: true });
    this.$nextTick(() => {
      this.row = this.$refs.row.$el;
      this.form = this.$refs.form.$el;
      addResizeListener(this.form, this.__handle);
    });
  },
  beforeDestroy() {
    removeResizeListener(this.form, this.__handle);
  },
};
</script>

<style lang="scss">
.data-grid-search-form {
  &-btn {
    font-size: 0 !important;
    letter-spacing: 0;
    .el-form-item {
      margin-bottom: 0 !important;
    }
    .el-button + .el-button {
      margin-left: 0 !important;
    }
    .el-button {
      margin-right: 14px;
      margin-bottom: 20px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .el-form {
    font-size: 0;
    .el-select,
    .el-input {
      width: 200px;
    }
  }
}
</style>
