<template>
    <span class="custom-tree-node" :class="{'is-disabled': disabled}" @dblclick="onDbClick" >
        <!-- <span class="tree-node-status"
        :class="{active: $refs.tree && $refs.tree.getCurrentNode() ? $refs.tree.getCurrentNode()[nodeKey] === data[nodeKey] : false}"
        v-if="!node.data[options.children] || !node.data[options.children].length"></span> -->
        
        <!-- 系统管理——组织机构专用结构 -->
        <template v-if="isOrganization" ref="label">
          <div style="display: flex; width: 85%">
            <div :class="tag === '公司' ? 'min-label' :'label'" v-if="!editable || (editable && !edit)">{{ currentValue }}</div>
            <div style="font-size: 14px; flex-shrink: 0">（{{userCount}}人）</div>
            <div class="tag" style="font-size: 14px;" v-if="tag === '公司'">公司</div>
          </div>
        </template>
        <template v-else>
          <span class="tree-node-label" v-if="!editable || (editable && !edit)">{{ currentValue }}</span>
          <input @blur.stop="onBlur" placeholder="请输入名称" autofocus ref="input" v-if="editable && edit" class="tree-node-input" :value="currentValue" />
        </template>
    </span>
</template>

<script>
export default {
  props: {
    node: Object,
    value: {},
    options: Object,
    editable: {
      type: Boolean,
      default: false
    },
    disabled: Boolean,
    isOrganization: {
      type: Boolean,
      default: false,
    },
    // organization: {
    //   type: Boolean,
    //   default: false,
    // },
    userCount: {
      type: Number,
      default: 0,
    },
    tag: {
      type: String,
      default: '部门'
    }
  },
  watch: {
    value () {
      this.currentValue = this.value
    }
  },
  data () {
    return {
      currentValue: '',
      edit: false
    }
  },
  methods: {
    onDbClick () {
      this.edit = true
      this.$nextTick(() => {
        this.$refs.input.focus()
      })
    },
    onBlur (e) {
      this.edit = false
      this.currentValue = e.target.value
      this.$emit('input', this.currentValue)
    }
  },
  mounted () {
    this.currentValue = this.value
  },
}
</script>

<style lang="scss">
.tree-node-input {
    width: 100%;
    font-size: 14px;
    outline: none;
    border: 0;
    background: 0;
    color: #606266;
    line-height: 19px;
}
.custom-tree-node {
    width: 100%;
    font-size: 0;
    .tag {
      white-space: nowrap;
      display: inline-block;
      padding: 2px 4px 3px;
      box-sizing: border-box;
      // float: right;
      border: 0px solid blue;    
      background: #DDEEFF;
      border-radius: 4px;
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      line-height: 17px;
      color: #409BFF;
      font-weight: 400;
    }
    .label {
      // width: 110px;
      // width: 100%;
      // max-width: 80px;
      display: inline-block;
      // line-height: 16px;
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      // vertical-align: bottom;
    }
    .min-label {
      bottom: 0;
      display: inline-block;
      // line-height: 16px;
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      vertical-align: bottom;
    }
}
.tree-node-label {
    font-size: 14px;
}
.tree-node-status {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 10px;
    background: #e3e4e5;
    &.active {
        background: #409eff;
    }
    & + .tree-node-label {
        vertical-align: middle;
    }
}
.custom-tree-node.is-disabled {
    .tree-node-label {
        color: rgb(153, 153, 153)
    }
}
</style>
