<template>
  <div class="table-search" :class="{ 'table-search-small': !isShowExcess }">
    <el-form
      class="table-search-form"
      ref="form"
      :model="model"
      :style="rowWidthStyle"
    >
      <el-row class="table-search-form-row" :gutter="20">
        <slot></slot>
      </el-row>
    </el-form>
    <div class="table-search-right">
      <el-button type="primary" @click="handleConfirm">{{
        confirmText
      }}</el-button>
      <el-button v-if="isReset" @click="handleReset">{{ restText }}</el-button>
      <el-button type="text" @click="cutShowExcess" v-if="isExcessBtn">
        {{ isShowExcess ? "收起" : "展开" }}
        <i class="el-icon-arrow-down" :class="{ isShow: isShowExcess }"></i>
      </el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "jc-data-grid-search",
  props: {
    isReset: {
      // 是否显示重置按钮
      type: Boolean,
      default: true,
    },
    simple: {
      // 是否启用只有一个搜索条件时，隐藏名称
      type: Boolean,
      default: true,
    },
    confirmText: {
      type: String,
      default: "查询",
    },
    restText: {
      type: String,
      default: "重置",
    },
    model: {
      type: Object,
    },
    defaultShowExcess: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isShowExcess: false, // 是否展示溢出
      itemLength: 1, // slot长度
      formWidth: 0, // 表单的大小
      VNode: [],
    };
  },
  mounted() {
    // 监听屏幕尺寸变化
    this.$nextTick(() => {
      this.onresize();
      window.addEventListener("resize", this.onresize);
    });
  },
  computed: {
    rowWidthStyle() {
      let width = "100%";

      if (this.itemLength == 1) {
        width = "400px";
      } else if (this.itemLength == 2) {
        width = "800px";
      } else if (this.itemLength == 3) {
        width = "1200px";
      }

      return `width:${width}`;
    },
    isExcessBtn() {
      let bool = false;

      if (this.VNode.length) {
        let num = Math.round(this.formWidth / this.VNode[0].elm.clientWidth);

        if (this.itemLength > num) {
          bool = true;
        }
      }

      return bool;
    },
  },
  watch: {
    isShowExcess: {
      immediate: true,
      handler: function (val) {
        console.log(this);
        this.$nextTick(() => {
          this.$parent.triggerResize();
        });
        // this.$emit('onsize')
      },
    },
    "$parent.tabActive": {
      immediate: true,
      handler: function (val) {
        this.isShowExcess = false;
        this.onresize();
      },
    },
  },
  methods: {
    cutShowExcess() {
      // 切换是否显示更多
      this.isShowExcess = !this.isShowExcess;
    },
    handleConfirm() {
      // 抛出事件
      this.$emit("confirm");
    },
    handleReset() {
      // 重置
      this.$refs.form.resetFields();
      this.$emit("reset");
    },
    getFormHandle() {
      // 获取 el-form 的句柄
      return this.$refs.form;
    },
    onresize() {
      this.$nextTick(() => {
        this.formWidth = this.$refs.form.$el.clientWidth;

        this.VNode = this.$slots.default.filter((item) => {
          return item.tag ? item.tag.includes("module-template") : false;
        });

        this.itemLength = this.VNode.length;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.table-search {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  display: flex;

  &-small {
    height: 55px;
    overflow: hidden;
  }

  &-form {
    max-width: 1920px;
  }

  &-right {
    width: 300px;
    height: 36px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.el-icon-arrow-down {
  transition: all 0.5s;
}

.isShow {
  transform: rotate(-180deg);
}
</style>
