<template>
  <div class="content-col data-col" :style="contentColStyle">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    collapse: Boolean,
  },
  computed: {
    contentColStyle() {
      if (this.collapse) return { width: "calc(100% - 20px)" };
      else return { width: "81%" };
    },
  },
};
</script>

<style lang="scss">
.content-col {
  height: 100%;
  &.data-col {
    float: left;
    width: 80%;
    background: #fff;
    overflow: hidden;
    padding: 28px 16px 0;
    transition: width 0.2s;
    // border: 1px solid rgb(233, 233, 233);
  }
  .table-wrapper {
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100% - 72px);
  }
}
</style>
