<template>
  <jc-left-right-Layout-view>
    <div class="tree-data-grid-left" slot="side">
      <jc-action-sheet
        v-if="actionSheet && actionSheet.length"
        :actions="actionSheet"
      ></jc-action-sheet>
      <jc-tree
        defaultExpandAll
        :data="categories"
        :props="treeProps"
        ref="tree"
        :tree-action-sheet="Boolean(actionSheet && actionSheet.length)"
        :draggable="draggable"
        @current-change="onCategoryChange"
        @node-drop-success="nodeDropSuccessHandler"
        :allow-drag="allowDragHandler"
        :allow-drop="allowDropHandler"
        @node-drop-failed="nodeDropFailedHandler"
      ></jc-tree>
    </div>
    <div class="searchbar">
      <jc-search
        @search="select(true)"
        v-model="keyword"
        :label="title"
        :placeholder="placeholder"
      ></jc-search>
      <slot name="header" />
    </div>
    <jc-table-template
      id="table-wrapper"
      :bottom="18"
      :total="total"
      :current-page="pageNum"
      :pagination-disabled="loading"
      :page-size="pageSize"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
      :el="'table-wrapper'"
    >
      <el-table
        highlight-current-row
        :data="list"
        :empty-text="emptyText"
        @current-change="onTableCurrentChange"
        @sort-change="onTableSortChange"
        @select-all="onTableSelectAll"
        @selection-change="onTableSelectionChange"
        @row-dbclick="onTableRowDbClick"
        @filter-change="onTableFilterChange"
        :show-overflow-tooltip="true"
        tooltip-effect="dark"
        stripe
        style="width: 100%"
      >
        <slot />
      </el-table>
      <template slot="btns">
        <slot name="btns" />
      </template>
    </jc-table-template>
    <slot name="modal" />
  </jc-left-right-Layout-view>
</template>

<script>
import Mixins from "../../util/dataGridMixins";
export default {
  name: "jc-tree-data-grid", // 树形组件过于复杂 正则匹配缓存组件监听不到
  mixins: [Mixins],
  props: {
    placeholder: {
      type: String,
      default: "",
    },
    actionSheet: {
      // 侧栏操作按钮
      type: Array,
      default() {
        return [];
      },
    },
    selectAsideApi: {
      // 侧栏数据查询接口
      type: String,
      required: true,
    },
    treeParams: {
      type: Object,
      default() {
        return {};
      },
    },
    treeProps: {
      type: Object,
      default() {
        return {};
      },
    },
    actionSheetHandler: {
      // 侧栏操作按钮处理函数（设置禁用项）
      type: Function,
      default() {
        return () => {};
      },
    },
    keywordKey: {
      // 关键字参数名 查询时使用
      type: String,
      default: "keyword",
    },
    categoryKey: {
      // 分类参数名 查询时使用
      type: String,
      required: true,
    },
    draggable: {
      type: Boolean,
    },
    nodeLevel: {
      // 表示几级节点开始允许触发节点改变事件拉取数据
      type: Number,
      default: -1,
    },
    nodeDropSuccessHandler: {
      type: Function,
      default() {
        return () => {};
      },
    },
    nodeDropFailedHandler: {
      type: Function,
      default() {
        return () => {};
      },
    },
    allowDragHandler: {
      type: Function,
      default() {
        return () => {};
      },
    },
    allowDropHandler: {
      type: Function,
      default() {
        return () => {};
      },
    },
  },
  created() {
    if (this.init) return;
    this.categoryId = this.$route.query.category;
    this.pageNum = this.$route.query.pageNum
      ? Number(this.$route.query.pageNum)
      : 1;
    this.pageSize = this.$route.query.pageSize
      ? Number(this.$route.query.pageSize)
      : 25;
    this.keyword = this.$route.query.keyword;
    this.selectAsideData();
  },
  activated() {
    if (this.init) this.select();
    else this.init = true;
  },
  computed: {
    requestParams() {
      return {
        [this.keywordKey]: this.keyword,
        [this.categoryKey]: this.categoryId,
      };
    },
  },
  methods: {
    selectLeaf(values, id) {
      if (!values) return null;
      for (let i = 0; i < values.length; ++i) {
        let val = values[i];
        if (val.children && val.children.length) {
          let leaf = this.selectLeaf(val.children, id);
          if (leaf) return leaf;
        } else {
          if (id !== undefined && val.id == id) {
            return val;
          } else if (id === undefined || id === null) return val;
        }
      }
      return null;
    },
    /**
     * @description 获取侧栏分类
     * @author yangyu
     */
    selectAsideData() {
      this.$api(this.selectAsideApi, { ...this.treeParams })()
        .then((res) => {
          if (res) {
            this.categories = res;
            this.$nextTick(() => {
              // 设置默认节点，category id为空则选中第一个
              let leaf = null;
              if (this.categoryId) {
                leaf = this.selectLeaf(this.categories, this.categoryId);
              } else {
                leaf = this.selectLeaf(this.categories); // 选取第一个节点
              }
              if (!leaf) leaf = this.selectLeaf(this.categories); // 选取第一个节点
              if (leaf && this.$refs.tree) {
                this.$refs.tree.setCurrentKey(leaf.id);
                this.currentLeafNode = this.$refs.tree.getNode(leaf.id);
                this.currentNode = this.currentLeafNode;
                if (this.currentLeafNode)
                  this.title = this.currentLeafNode.data.name;
                if (
                  this.actionSheet &&
                  this.actionSheet.length &&
                  typeof this.actionSheetHandler === "function"
                )
                  this.actionSheetHandler(this.currentNode);
              }
              if (leaf) {
                this.categoryId = leaf.id;
                this.select(true);
              }
            });
          }
        })
        .catch((err) => {
          this.alert(err, "error");
        });
    },
    select(reload, params = {}, api) {
      return this.__select(reload, params, api);
    },
    /**
     * @description 分类改变
     * @author yangyu
     */
    onCategoryChange(data, node, leaf) {
      if (this.nodeLevel === -1 && leaf) {
        this.currentLeafNode = leaf;
        this.categoryId = data.id;
        this.title = data.name;
        this.keyword = "";
        this.select(true);
      } else if (this.nodeLevel <= node.level && this.nodeLevel !== -1) {
        this.currentLeafNode = node;
        this.categoryId = data.id;
        this.title = data.name;
        this.keyword = "";
        this.select(true);
      }
      this.currentNode = node;
      if (
        this.actionSheet &&
        this.actionSheet.length &&
        typeof this.actionSheetHandler === "function"
      ) {
        this.actionSheetHandler(this.currentNode);
      }
    },
    onNodeDropSuccess() {},
  },
  data() {
    return {
      categories: [],
      loading: false,
      categoryId: "",
      title: "",
      keyword: "",
      init: false,
      currentNode: null,
      currentLeafNode: null,
    };
  },
};
</script>

<style lang="less" scoped>
.content-col {
  .tree-data-grid-left {
    height: 100%;
  }
  height: 100%;
  .searchbar {
    // font-size: 0;
    // overflow: hidden;
    // margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    height: 100px;
    .functions {
      height: 20px;
      // margin-top: 20px;
    }
    .search-wrapper {
      // width: 400px;
    }
  }
}
.table-news-img {
  width: 44px;
  height: 44px;
  border-radius: 6px;
  border: 1px solid #409eff;
}
.jc-modal-btn {
  margin-left: 10px;
}
</style>
