<template>
  <div class="data-grid-container" ref="self">
    <div class="data-grid-container-content">
      <div ref="search" id="search" class="search-form-wrapper">
        <slot name="search"></slot>
      </div>
      <div ref="header" class="search-form-header">
        <slot name="header"></slot>
      </div>
      <div
        v-if="!customBody"
        class="table-box"
        :style="{ height: bodyHeight + 'px' }"
      >
        <slot></slot>
      </div>
      <div
        v-if="customBody"
        ref="customBody"
        class="customBody"
        :style="{ height: bodyHeight + 'px' }"
      >
        <slot name="customBody" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "jc-data-grid-head",
  props: {
    customBody: {
      type: Boolean,
      default: false,
    },
    tabActive: {
      type: String,
    },
  },
  data() {
    return {
      bodyHeight: 0,
    };
  },
  methods: {
    triggerResize() {
      this.$nextTick(() => {
        let search = 0;
        let header = 0;
        let self = 0;

        search = this.$refs.search.offsetHeight;
        header = this.$refs.header.offsetHeight;
        self = this.$refs.self.clientHeight;

        this.bodyHeight = self - (search + header);

        console.log(this.bodyHeight);
      });
    },
  },
  mounted() {
    this.triggerResize();
  },
};
</script>

<style lang="scss" scoped>
.data-grid-container {
  height: 100%;
  overflow: hidden;
  // background: pink;

  &-content {
    background: #ffffff;
    border-radius: 4px;
    width: 100%;
    height: 100%;

    .table-box {
      width: 100%;
      box-sizing: border-box;
      padding: 12px 12px 0;
    }
  }

  .search-form-wrapper {
    padding: 20px 14px 0;
  }

  .search-form-header {
    padding: 0 14px;
  }

  .data-list {
    margin-top: 14px;
    height: 100%;

    .data-list-body {
      padding: 0 14px;
      height: calc(100% - 66px);
    }
  }

  .customBody {
    overflow: auto;
  }

  .table-wrapper {
    overflow-x: hidden;
    overflow-y: auto;
    height: calc(100%);
  }
}
</style>
