<template>
  <div class="table-wrapper" :class="cls" ref="table">
    <slot></slot>
    <div class="table-footer" ref="footer">
      <el-pagination
        small
        class="table-footer-pagination"
        background
        :disabled="paginationDisabled"
        :layout="layout"
        :page-sizes="pageSizes"
        :total="total"
        :current-page="currentPage"
        :page-size="pageSize"
        @size-change="onSizeChange"
        @current-change="onCurrentChange"
      >
      </el-pagination>
      <div class="table-footer-function">
        <slot name="btns"></slot>
      </div>
    </div>
  </div>
</template>

<script>
import dom from "../../util/dom.js";
import {
  addResizeListener,
  removeResizeListener,
} from "../../util/resize-manager.js";
import debounce from "lodash.debounce";

export default {
  name: "jc-table-template",
  props: {
    layout: {
      type: String,
      default: "total, sizes, prev, pager, next",
    },
    sidajingangLayout: {
      type: [Object, Boolean],
      default() {
        return {
          left: ["add", "edit", "delete"],
          right: ["refresh"],
        };
      },
    },
    paginationDisabled: {
      type: Boolean,
      default: false,
    },
    pageSizes: {
      type: Array,
      default: () => [10, 25, 50, 100, 200, 1000],
    },
    total: {
      type: Number,
      default: 0,
      required: true,
    },
    currentPage: {
      type: Number,
      default: 1,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 25,
      required: true,
    },
    el: {
      type: [String, Object],
      default: "main-content-con",
    },
    bottom: {
      type: Number,
      default: 0,
    },
    fixed: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      parent: null,
      cls: `table-${new Date().getTime()}`,
      sidatianwangMapping: {
        add: {
          text: "新增",
          icon: "jc-icon icon-add2",
          cb(e) {
            this.me.$emit("add", e);
          },
          me: this,
        },
        edit: {
          text: "编辑",
          icon: "jc-icon icon-edit",
          cb(e) {
            this.me.$emit("edit", e);
          },
          me: this,
        },
        delete: {
          text: "删除",
          icon: "jc-icon icon-delete",
          cb(e) {
            this.me.$emit("delete", e);
          },
          me: this,
        },
        refresh: {
          text: "刷新",
          icon: "jc-icon icon-refresh",
          cb(e) {
            this.me.$emit("refresh", e);
          },
          me: this,
        },
      },
      table: null,
      body: null,
      header: null,
      contentEl: null,
      scollWidth: 17,
      height: 0,
    };
  },
  methods: {
    dispatch(item) {
      item.cb(this.that);
    },
    onCurrentChange(val) {
      this.$emit("current-change", val);
    },
    onSizeChange(val) {
      this.$emit("size-change", val);
    },
    __handleScroll() {},
    onResize() {
      let doms = document.getElementsByClassName(this.cls);
      if (doms && doms.length) {
        let rect = doms[0].getBoundingClientRect();
        // this.$refs.table1.$el.style.height = `${window.innerHeight - rect.top}px`
        this.height = window.innerHeight - rect.top - 50 - this.bottom; // 50 工具栏
        this.$emit("on-resize", this.height);
      }
    },
    __onResize() {
      setTimeout(() => {
        let doms = document.getElementsByClassName(this.cls);
        if (doms && doms[0]) {
          removeResizeListener(doms[0], this.onResize);
          addResizeListener(doms[0], this.onResize);
          // addResizeListener(document.documentElement, this.onResize)
          window.onresize = this.onResize;
        }
      }, 500);
    },
    handleScroll(e) {
      if (!this.$refs.footer || !this.$refs.table) return;
      let tableRect = this.$refs.table.getBoundingClientRect();
      if (!this.table) {
        for (let i = 0; i < this.$refs.table.children.length; ++i) {
          let child = this.$refs.table.children[i];
          if (child.className.indexOf("el-table") !== -1) {
            this.table = child;
            break;
          }
        }
      }
      if (!this.table) return;
      if (!this.body) {
        for (let i = 0; i < this.table.children.length; ++i) {
          let child = this.table.children[i];
          if (child.className.indexOf("el-table__body-wrapper") !== -1) {
            this.body = child;
          }
          if (child.className.indexOf("el-table__footer-wrapper") !== -1) {
            this.body = child;
          }
          if (child.className.indexOf("el-table__header-wrapper") !== -1) {
            this.header = child;
          }
        }
      }
      if (this.body) {
        // offsetTop 内容区距离顶部的距离
        if (
          this.body.getBoundingClientRect().bottom >=
          window.innerHeight - 15
        ) {
          this.$refs.footer.style.width = `${this.$refs.table.offsetWidth}px`;
          this.$refs.footer.style.left = tableRect.left + "px";
          this.$refs.footer.style.bottom = this.bottom + "px";
          dom.addClass(this.$refs.footer, "table-footer-fixed");
        } else {
          this.$refs.footer.style.width = `${this.$refs.table.offsetWidth}px`;
          this.$refs.footer.style.left = `0px`;
          this.$refs.footer.style.bottom = "0px";
          dom.removeClass(this.$refs.footer, "table-footer-fixed");
        }
      }
      if (e) {
        if (e.target.scrollTop > 0) {
          dom.addClass(this.$refs.table, "border");
        } else {
          dom.removeClass(this.$refs.table, "border");
        }
      }
      // if (e && this.header && this.body) {
      //    if (!this.headerOffsetTop) this.headerOffsetTop = this.header.getBoundingClientRect().top
      //   if (e.target.scrollTop >= this.header.offsetHeight) {
      //     this.header.style.left = tableRect.left + 'px'
      //     this.header.style.top = `${this.headerOffsetTop}px`
      //     this.header.style.width = `${this.$refs.table.offsetWidth - this.scollWidth}px`
      //     // this.header.style.top = top + 'px'
      //     dom.addClass(this.header, 'table-header-fixed')
      //   } else {
      //     this.header.style.left = `0px`
      //     this.header.style.top = '0px'
      //     this.header.style.width = `${this.$refs.table.offsetWidth}px`
      //     dom.removeClass(this.header, 'table-header-fixed')
      //   }
      // } else if (this.header && this.body) {
      //   if (dom.hasClass(this.header, 'table-header-fixed')) {
      //     this.header.style.left = tableRect.left + 'px'
      //     this.header.style.top = `${this.headerOffsetTop}px`
      //     this.header.style.width = `${this.$refs.table.offsetWidth - this.scollWidth}px`
      //   } else {
      //     this.header.style.width = `${this.$refs.table.offsetWidth}px`
      //   }
      // }
    },
  },
  updated() {
    if (!this.fixed) {
      this.__onResize();
      return;
    }
    this.$nextTick(() => {
      this.handleScroll();
    });
  },
  activated() {
    if (!this.fixed) {
      this.__onResize();
      return;
    }
    this.$nextTick(() => {
      this.handleScroll();
    });
  },
  mounted() {
    if (!this.fixed) {
      this.__onResize();
      return;
    }
    this.__handleScroll = debounce(this.handleScroll, 20, { leading: true });
    this.scollWidth = dom.getScrollBarWidth();
    setTimeout(() => {
      this.parent = this.$parent;
      for (let i = 0; i < this.$refs.table.children.length; ++i) {
        let child = this.$refs.table.children[i];
        if (child.className.indexOf("el-table") !== -1) {
          this.table = child;
          break;
        }
      }
      if (this.table) {
        addResizeListener(this.table, this.__handleScroll);
      }
      this.handleScroll();
      this.contentEl =
        typeof this.el === "string"
          ? document.getElementById(this.el)
          : this.el;
      if (this.contentEl)
        dom.off(this.contentEl, "scroll", this.__handleScroll, true);
      if (this.contentEl)
        dom.on(this.contentEl, "scroll", this.__handleScroll, true);
      addResizeListener(
        document.documentElement || document.body,
        this.__handleScroll
      );
    }, 500);
  },
  beforeDestroy() {
    if (!this.fixed) {
      removeResizeListener(this.$refs.table, this.onResize);
      return;
    }
    removeResizeListener(this.table, this.__handleScroll);
    if (this.contentEl)
      dom.off(this.contentEl, "scroll", this.__handleScroll, true);
    removeResizeListener(
      document.documentElement || document.body,
      this.__handleScroll
    );
  },
};
</script>

<style lang="scss">
.table-wrapper {
  clear: both;
  overflow-y: auto;

  &.border {
    border-top: 1px solid #ebeef5;
  }

  .table-header-fixed {
    // 固定表头
    position: fixed;
    // top: 122px;
    z-index: 1999;
    border-top: 1px solid #ebeef5;
    border-left: 1px solid #ebeef5;
  }

  .table-footer {
    position: relative;
    height: 50px;
    line-height: 50px;
    // border: 1px solid #ebeef5;
    // border-top: 0;
    box-sizing: border-box;

    &.table-footer-fixed {
      position: fixed;
      bottom: 0;
      background: #fff;
      z-index: 1999;
      border: 0;
    }

    .table-footer-pagination {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);

      * {
        vertical-align: middle;
      }
    }

    .table-footer-function {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
    }

    .el-pagination__sizes {
      margin-top: -5px;
    }
  }
}

.sidatianwang {
  width: 100%;
  height: 25px;
  clear: both;

  .left-guardian {
    float: left;
  }

  .right-guardian {
    float: right;
  }

  .guardians {
    overflow: hidden;

    .guardian {
      float: left;
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }

      .link {
        font-size: 15px;
        text-decoration: none;

        i {
          margin-right: 10px;
        }

        &:hover {
          i::before {
            color: #409eff;
          }

          color: #409eff;
        }
      }
    }
  }
}
</style>
