<template>
  <div class="cardv2">
    <div
      class="cardv2-header"
      :class="{ collapse: !sync }"
      :style="{
        background: headerBackgroundColor,
        color: headerColor,
        borderColor: headerBorderColor,
      }"
      @click.stop="toggle"
    >
      <div class="left">
        <span class="title" :style="{ color: headerColor }">{{ title }}</span>
      </div>
      <div class="right">
        <i class="jc-icon icon-arrow-up" :class="{ collapse: !active }"></i>
      </div>
    </div>
    <el-collapse-transition>
      <div class="cardv2-body" v-show="active"><slot /></div>
    </el-collapse-transition>
  </div>
</template>

<script>
export default {
  name: "jc-card-layout",
  props: {
    title: {},
    headerBackgroundColor: {
      // 头部颜色
      type: String,
      default: "#fff",
    },
    headerColor: {
      type: String,
      default: "#606060",
    },
    headerBorderColor: {
      type: String,
      default: "rgba(216,216,216,1)",
    },
    collapse: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    collapse: {
      immediate: true,
      handler() {
        this.active = !this.collapse;
        this.sync = this.active;
      },
    },
  },
  data() {
    return {
      active: true,
      sync: true, // 同步底部边框
    };
  },
  methods: {
    toggle() {
      this.active = !this.active;
      if (!this.active) {
        setTimeout(() => {
          this.sync = false;
        }, 300);
      } else {
        this.sync = true;
      }
    },
  },
};
</script>

<style lang="scss">
.cardv2 {
  // border:1px solid rgba(216,216,216,1);
  background: #fff;
  &-header {
    padding: 0 18px;
    // border: 1px solid rgba(216,216,216,1);
    line-height: 34px;
    height: 34px;
    overflow: hidden;
    user-select: none;
    .left {
      float: left;
      .title {
        font-size: 18px;
        color: #606060;
      }
    }
    .right {
      float: right;
      i {
        display: inline-block;
        transition: all 0.4s;
        cursor: pointer;
        &.collapse {
          transform: rotate(180deg);
        }
      }
    }
    &.collapse {
      border-bottom: none;
    }
  }
  &-body {
    padding: 20px;
    border-top: 0;
    // .el-row:last-child {
    //     .el-form-item {
    //         margin-bottom: 0!important;
    //     }
    // }
  }
}
</style>
