<template>
  <div class="search-wrapper">
    <label class="search-field-label" :title="label">{{ label }}</label>
    <input
      class="el-input__inner search-input"
      :style="{ width: width + 'px' }"
      @keyup.stop="onKeyup"
      :placeholder="placeholder"
      :value="currentValue"
      @input.stop="onInput"
    />
    <el-tooltip content="搜索" placement="top">
      <button class="search-btn" @click.stop="onSearch">
        <i class="jc-icon icon-search"></i>
      </button>
    </el-tooltip>
  </div>
</template>

<script>
export default {
  name: "jc-search",
  props: {
    label: String,
    value: {},
    width: {
      type: Number,
      default: 334,
    },
    placeholder: String,
  },
  data() {
    return {
      currentValue: "",
    };
  },
  watch: {
    value(val) {
      this.currentValue = val;
    },
  },
  methods: {
    onInput(e) {
      if (!e) return;
      this.currentValue = e.target.value;
      this.$emit("input", e.target.value);
    },
    onSearch(e) {
      this.$emit("search", this.currentValue, e);
    },
    onKeyup(e) {
      let code = e.charCode || e.keyCode;
      if (code === this.$const.key.ENTER) {
        this.onSearch(e);
      }
    },
  },
};
</script>

<style lang="scss">
.search-wrapper {
  float: left;
  position: relative;
  .search-field-label {
    font-size: 16px;
    line-height: 36px;
    margin-right: 10px;
    max-width: 300px;
    vertical-align: bottom;
    overflow: hidden;
    display: inline-block;
    text-overflow: ellipsis;
    float: left;
    white-space: nowrap;
  }
  .search-input {
    height: 36px;
    line-height: 36px;
    width: 334px;
    font-size: 16px;
    padding-right: 51px;
  }
  .search-btn {
    // display: inline-block;
    width: 36px;
    height: 36px;
    // vertical-align: middle;
    // position: absolute;
    margin-left: -36px;
    background: #409eff;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
    line-height: 36px;
    outline: none;
    border: 0;
    top: 0;
    right: 0;
  }
}
</style>
