<template>
  <span class="badge" v-if="num">
    <span>{{ num > max ? "99+" : num }}</span>
  </span>
</template>

<script>
export default {
  name: "jc-badge",
  props: {
    num: Number,
    max: {
      type: Number,
      default: 99,
    },
  },
};
</script>

<style lang="scss">
.badge {
  width: 20px;
  height: 27px;
  line-height: 27px;
  text-align: center;
  font-size: 12px;
  color: #fff;
  display: inline-block;
  margin-left: 2px;
  position: absolute;
  background: url(data:image/png;base64,)
    no-repeat 50%;
  span {
    position: absolute;
    top: 50%;
    left: 50%;
    display: inline-block;
    transform: translate(-50%, -50%) scale(0.8);
  }
}
</style>
