<template>
  <div class="content-col side-grid-col" :style="treeColStyle">
    <div class="side-grid-wrapper" :style="treeDataWrapperStyle">
      <slot></slot>
    </div>
    <div class="collapse-wrapper" :style="collapseWrapperStyle">
      <i
        :class="{ collapse: collapse }"
        @click.stop="trigger"
        class="jc-icon icon-collapse"
      ></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    collapse: Boolean,
  },
  watch: {
    collapse(val) {
      this.currentCollapse = val;
    },
  },
  computed: {
    treeColStyle() {
      if (this.currentCollapse) return { width: "20px" };
      else return { width: "19%" };
    },
    treeDataWrapperStyle() {
      if (this.currentCollapse) return { width: "0%", padding: "0", border: 0 };
      else return { width: "calc(100% - 20px)" };
    },
    collapseWrapperStyle() {
      if (this.currentCollapse) return { width: "20px" };
      else return { width: "20px" };
    },
  },
  data() {
    return {
      currentCollapse: false,
    };
  },
  methods: {
    trigger() {
      this.currentCollapse = !this.currentCollapse;
      this.$emit("update:collapse", this.currentCollapse);
      this.$emit("side-collapse");
    },
  },
};
</script>

<style lang="scss">
.content-col {
  height: 100%;
  &.side-grid-col {
    float: left;
    width: 20%;
    overflow-x: hidden;
    overflow-y: auto;
    height: 100%;
    transition: width 0.2s;
    .side-grid-wrapper {
      float: left;
      width: 95%;
      padding: 20px 10px;
      background: #fff;
      height: 100%;
      transition: width 0.2s;
      overflow-x: hidden;
      &::-webkit-scrollbar-track-piece {
        background-color: #fff;
      }
      &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
      }
      &::-webkit-scrollbar-thumb {
        position: relative;
        display: block;
        width: 0;
        height: 0;
        cursor: pointer;
        border-radius: inherit;
        background-color: rgba(144, 147, 153, 0.3);
        transition: background-color 0.3s;
      }
      &:hover::-webkit-scrollbar-thumb {
        background-color: rgba(144, 147, 153, 0.5);
      }
      // border: 1px solid rgb(233, 233, 233);
      // border: 1px solid #ebeef5;
    }
    .collapse-wrapper {
      float: left;
      width: 5%;
      // margin: 0 5px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      i {
        cursor: pointer;
        &.collapse {
          transform: rotate(180deg);
        }
      }
    }
  }
}
</style>
