<template>
  <ul class="action-sheet">
    <li
      class="action-sheet-item"
      :style="{
        width: actions.length < 5 ? `${(1 / actions.length) * 100}%` : '20%',
      }"
      v-for="(item, index) in actions"
      v-if="!(actions.length > 5 && index >= 4)"
      :class="{ 'is-disabled': item.disabled }"
      @click.stop="
        !item.disabled && typeof item.action === 'function' && item.action()
      "
      :key="index"
      :title="item.name"
    >
      <i :class="item.icon" v-if="item.icon"></i
      ><span class="action-name">{{ item.name }}</span>
    </li>
    <li v-if="actions.length > 5" class="action-sheet-item">
      <el-dropdown @command="onCommand" trigger="click">
        <div class="dropdown-menu-trigger">
          <i class="jc-icon icon-more"></i><span class="action-name">更多</span>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item, index) in actions"
            :key="index"
            v-if="index + 1 >= 5"
            class="dropdown-action-item"
            :class="{ 'is-disabled': item.disabled }"
            :command="index"
            ><i
              :class="item.icon"
              class="dropdown-action-icon"
              v-if="item.icon"
            ></i
            ><span class="dropdown-action-name">{{
              item.name
            }}</span></el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </li>
  </ul>
</template>

<script>
export default {
  name: "jc-action-sheet",
  props: {
    actions: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods: {
    onCommand(index) {
      !this.actions[index].disabled &&
        typeof this.actions[index].action === "function" &&
        this.actions[index].action();
    },
  },
};
</script>

<style lang="scss">
.dropdown-action-item {
  &.is-disbaled {
    cursor: not-allowed;
    color: rgb(153, 153, 153);
  }
}
.dropdown-menu-trigger {
  &:hover {
    color: #409eff;
  }
}
.dropdown-action-icon {
  margin-right: 4px;
  vertical-align: middle;
}
.dropdown-action-name {
  vertical-align: middle;
}

.action-sheet {
  overflow: hidden;
  margin-bottom: 20px;
  .action-sheet-item {
    float: left;
    width: 20%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    cursor: pointer;
    text-align: center;
    i {
      vertical-align: middle;
      margin-right: 4px;
    }
    .action-name {
      vertical-align: middle;
    }
    &:hover {
      color: #409eff;
    }
    &.is-disabled {
      cursor: not-allowed;
      color: rgb(153, 153, 153);
    }
  }
}
</style>
