import Area from "./area"
import Areas from "./areasDetail"
import Badge from "./badge"
import Collapse from "./collapse"
import DragList from "./dragList"
import DataGridHead from "./dataGridHead"
import DataGridTableTemplate from './dataGridTableTemplate'
import DataGridSearchForm from './dataGridSearchForm'
import DataGrid from './dataGrid'
import CardLayout from './cardLayout'
import CardDetail from './cardDetail'
import TreeDataGrid from "./treeDataGrid"
import TabjcDataGrid from "./tabDataGrid"
import Editor from "./editor"
import ActionSheet from "./actionSheet"
import CollectTree from "./collectTree"
import Tree from "./tree"
import Search from "./search"
import LeftRightLayout from "./leftRightLayout"
import DataGridSearch from "./dataGridSearch"
import DataGridSearchTemplate from "./dataGridSearch/template.js"
import Icons from "./icons"
import CountUp from "./countUp"
const components = [
  Area,
  Areas,
  Badge,
  Collapse,
  DragList,
  DataGridTableTemplate,
  DataGridHead,
  DataGridSearchForm,
  DataGrid,
  CardLayout,
  CardDetail,
  TreeDataGrid,
  Editor,
  ActionSheet,
  CollectTree,
  Tree,
  Search,
  LeftRightLayout,
  TabjcDataGrid,
  DataGridSearch,
  DataGridSearchTemplate,
  Icons,
  CountUp
]
const install = function (Vue) {
  if (install.installed) return
  components.map((component) => Vue.component(component.name, component))
}

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  Area,
  Areas,
  Badge,
  Collapse,
  DragList,
  DataGridTableTemplate,
  DataGridHead,
  DataGridSearchForm,
  DataGrid,
  CardLayout,
  CardDetail,
  TreeDataGrid,
  Editor,
  ActionSheet,
  CollectTree,
  Tree,
  Search,
  LeftRightLayout,
  TabjcDataGrid,
  DataGridSearch,
  DataGridSearchTemplate,
  Icons,
  CountUp
}
