<template>
  <div class="detail-item">
    <h3 class="detail-item-title">
      <span class="detail-item-icon"></span>{{ title }}
    </h3>
    <div class="detail-item-width">
      <el-collapse-transition>
        <div v-show="active">
          <slot></slot>
        </div>
      </el-collapse-transition>
      <div class="operation-item" v-if="isCollapse">
        <span class="content" @click="active = !active"
          ><i
            class="jc-icon icon-collapse"
            :class="{ 'icon-collapse-bottom': !active }"
          ></i
          >{{ !active ? "展开" : "收起" }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "jc-collapse",
  props: {
    title: String,
    isCollapse: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      active: true,
    };
  },
};
</script>

<style lang="scss">
.detail-item {
  .detail-item-title {
    padding: 10px 15px;
    vertical-align: middle;
    .detail-item-icon {
      display: inline-block;
      width: 4px;
      height: 16px;
      background: #0097fe;
      margin: 0px 5px -3px 5px;
    }
  }
  .detail-item-width {
    width: 95%;
    font-size: 14px;
    margin: auto;
    overflow: hidden;
    transition: height 5s;
    border-bottom: 1px dashed #e4e4e4;
  }
}
.operation-item {
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  color: #409bff;
  padding: 10px;
  & .content:hover {
    cursor: pointer;
    display: inline;
  }
  .icon-collapse {
    display: inline-block;
    transform: rotate(90deg);
    font-size: 14px;
    margin-right: 10px;
    transition: all 0.5s;
  }
  .icon-collapse-bottom {
    display: inline-block;
    transform: rotate(-90deg);
    transition: all 0.5s;
  }
}
</style>
